.container {
  margin: 0;
  height: 100vh;
}

.error-pg{
  background-color: $bg-color;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.series-title{
  background-image: linear-gradient(to bottom, rgba(60, 60, 60, 0.8), rgba(255, 255, 255, 0));
}

h1{
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 0;
  margin-top: 0;
  padding: 20px 0 0 40px;
  color: $heading-color;
}

h2{
  margin-top: 0;
  font-size: 20px;
  padding: 0 0 5px 40px;
  color: $heading-color;
}