.bottom-container{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 25vh;
    background-color: $bg-color;
}

.bottom-options{
    width: 100vw;
    display: flex;
    height: 40px;
    align-items: center;
    border-bottom: 2px solid rgb(80, 80, 80);
}

.option{
    padding: 5px 20px;
    margin-right: 20px;
    margin-bottom: -9px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1em;
    cursor: pointer;
}

.option:hover{
    font-weight: 600;
}

.option-focus{
    border-bottom: 2px solid $green-highlight;
    font-weight: 600;
}

.logo{
    margin-left: auto;
    margin-right: 10px;
    height: 20px;
}

.general-info, .cast-info, .awards-info{
    width: 100vw;
    display: flex;
    align-items: center;
    padding-top: 10px;
    height: 100px;
}

.actions{
    display: flex;
    justify-content: space-around;
    width: 32%;
}

.action{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 20px;
}

.action-img{
    height: 30px;
}

.action-text{
    color: #9b9b9b;
}

.synopsis{
    width: 68%;
    padding-right: 20px;
    margin-left: 20px;
}

.cast, .awards{
    margin-left: 20px;
}