.side-bar{
    height: 75vh;
    width: 30vw;
    max-width: 400px;
    right: 0;
    top: 0;
    overflow: auto;
    background-color: rgba(0,0,0,0.7);
    position: fixed;
    z-index: 1;
    box-shadow: -300px 0 200px rgba(0,0,0,0.8);
}

.close-search{
    display: flex;
    justify-content: flex-end;
    height: 20px;
    margin: 20px;
}

.seasons{
    display: flex;
    justify-content: start;
}

.season{
    margin-right: 20px;
    margin-left: 10px;
    background-color: transparent;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    padding-bottom: 5px;
    cursor: pointer;
}

.season-focus{
    border-bottom: 2px solid $green-highlight;
    margin-bottom: -2px;
}

.episode-list{
    margin-left: 20px;
}

.episode-headline{
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.episode-play{
    padding: 5px 30px 5px 5px;
    img{
        height: 25px;
    }
}

.episode-img{
    width: 100%;
    display: flex;
    justify-content:start;
    img{
        width: 90%;
    }
}

.episode-title{
    font-size: 0.9rem;
    padding: 10px 0;
}

.episode-info{
    width: 90%;
    padding: 5px 0;
    font-size: 0.8rem;
}